
#app {
  background-color: #2A2A35;
}

* {
    margin: 0;
    padding: 0;
}
body{
    background-color: #2A2A35;
}
.container {
    margin: 0 auto;
    width: 1000px;
    height: 100%;
}
h1 {
    text-align: center;
    margin-top: 100px;
    font-family: Roboto;
    font-style: normal;
    font-size: 60px;
    color: #D2C69E;
}
h2 {
    text-align: center;
    font-family: Roboto;
    font-style: normal;
    font-size: 40px;
    color: #D2C69E;
}
h3 {
    width: auto;
    text-align: left;
    font-family: Roboto;
    font-style: normal;
    font-size: 20px;
    color: #D2C69E;
}
p {
    margin-top: 15px;
    color: #FFFFFF;
}
a{
    color: #D2C69E;
}
.form{
    margin-top: 35px;
}
.form_item{
    width: 340px;
    min-height: 50px;
    margin: 10px auto 0 auto;
}
input {
    text-align: center;
    width: 100%;
    height: 30px;
    border-radius: 15px;
}
button {
    display: block;
    margin: 0 auto;
    font-family: Roboto;
    font-style: normal;
    font-size: 20px;
    margin-top: 15px;
    width: 200px;
    height: 60px;
    border-radius: 30px;
}
p{
    text-align: center;
}
button h3 {
    text-align: center;
}

